body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

#bcg-video {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  object-fit: cover;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: block; }

.video:before {
  background-image: url(data:image/png;base64,);
  position: fixed;
  content: ' ';
  min-width: 100%;
  min-height: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: .8;
  overflow: hidden; }

.fp_button {
  z-index: 150;
  position: absolute;
  background-color: #FFC600;
  color: black;
  border: 2px solid black;
  text-align: center;
  cursor: pointer; }
  .fp_button:hover {
    background: #cf7e00;
    -webkit-transition: ease-in .2s;
    transition: ease-in .2s; }

.lang {
  top: 20px;
  right: 20px;
  padding: 5px 20px;
  font-size: 16px; }
  .lang_en {
    right: 100px; }

.sect {
  bottom: 150px;
  padding: 15px;
  width: calc(100vw / 8);
  min-width: 100px; }
  .sect_peop {
    left: calc(100vw / 10); }
  .sect_mach {
    right: calc(100vw / 10); }

a,
a:active,
a:visited,
a:hover {
  color: #000;
  text-decoration: none; }

footer {
  color: ivory; }
  footer a,
  footer a:visited,
  footer a:active,
  footer a:link {
    color: ivory; }
  footer .fab {
    color: ivory; }

.about-people {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  justify-content: center;
  grid-gap: 30px; }
  .about-people .wide {
    grid-column: 1/3; }
  .about-people img {
    width: 200px;
    display: block;
    margin: 20px auto;
    position: relative !important; }

.clients {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-template-rows: repeat(auto-fill, 100px);
  grid-auto-flow: dense;
  grid-gap: 10px;
  box-sizing: border-box;
  overflow: hidden; }
  .clients img {
    padding: 0; }
  .clients .client-narrow {
    grid-column: span 1;
    grid-row: span 1;
    height: 100%;
    width: 100%; }
  .clients .client-wide {
    grid-column: span 2;
    grid-row: span 2;
    width: 100%;
    height: 100%;
    cursor: pointer; }

nav {
  display: flex;
  flex-flow: column;
  color: black;
  position: fixed;
  /* z-index and position are bot required so that mesh wouldn't go over the content */
  z-index: 6;
  top: 10px;
  width: calc(100vw - 20px);
  margin-left: -38px; }
  nav * {
    display: inline-flex;
    justify-content: space-between; }
  nav .mainNav * {
    background: #FFC600;
    margin: 0 5px;
    padding: 2px 5px; }
  nav .languages * {
    background: #FFC600;
    margin: 0 5px;
    padding: 2px 5px; }
  nav ul {
    list-style-type: none; }
    nav ul li {
      display: inline; }
    nav ul:hover {
      cursor: pointer; }

* {
  box-sizing: border-box; }

html {
  background-color: #282c34;
  color: ivory; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

main {
  position: relative;
  margin: 20px;
  min-height: calc(3*(100vh - 40px));
  min-width: calc(100vw - 60px); }

section, footer {
  min-height: 95vh;
  background: rgba(0, 0, 0, 0.65);
  border: 2px solid black;
  padding: 20px;
  margin-bottom: 20px;
  position: relative;
  /* z-index and position are both required so that mesh wouldn't go over the content */
  z-index: 5; }

section {
  align-items: center;
  justify-content: center;
  display: flex; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

